<template>
    <div class="order-filter">
        <div class="wrapper">
            <div class="filter-container">
                <!-- status filter -->
                <div class="filter-container__item">
                    <form-select
                        v-model="searchReturnObject.status"
                        :items="searchStatusOptions"
                        :field.sync="searchReturnObject.status"
                        :label="'Order Status'"
                        name="Order Status"
                    />
                </div>
                <!-- status filter -->

                <!-- search field filter -->
                <div class="filter-container__item">
                    <form-input
                        v-model="searchReturnObject.query"
                        :field.sync="searchReturnObject.query"
                        legend="Search query"
                        placeholder="Search for"
                        name="Search"
                        type="text"
                    />

                    <form-select
                        v-if="focusState || mobileState"
                        v-model="searchReturnObject.search_by"
                        label="Search for"
                        name="Search for"
                        :items="searchFieldOptions"
                        :field.sync="searchReturnObject.search_by"
                    />
                </div>
                <!-- search field filter -->

                <!-- status filter -->
                <div class="filter-container__item">
                    <form-select
                        v-model="searchReturnObject.per_page"
                        :items="rowsPerPageList"
                        :field.sync="searchReturnObject.per_page"
                        :label="'Per page'"
                        name="per page"
                    />
                </div>
                <!-- status filter -->
            </div>
        </div>
    </div>
</template>

<script>
import { debounce } from 'lodash'

export default {
    data: () => ({
        rowsPerPageList: [
            {
                value: 5,
                text: '5'
            },
            {
                value: 10,
                text: '10'
            },
            {
                value: 25,
                text: '25'
            },
            {
                value: 50,
                text: '50'
            },
            {
                value: 100,
                text: '100'
            }
        ],
        focusState: 'open',
        mobileState: false,
        page: null,
        query: '',
        search_by: 'title',
        searchFieldOptions: [
            {
                value: 'title',
                text: 'Title'
            },
            {
                value: 'orderid',
                text: 'Order ID'
            }
        ],
        searchStatusOptions: [
            {
                value: 'any',
                text: 'All'
            },
            {
                value: 'PAID',
                text: 'Paid (not assigned to writer)'
            },
            {
                value: 'UNPAID',
                text: 'Unpaid'
            },
            {
                value: 'PENDING',
                text: 'In Progress (assigned to writer)'
            },
            {
                value: 'COMPLETE',
                text: 'Completed'
            },
            {
                value: 'REVISION',
                text: 'In Revision'
            }
        ],
        searchReturnObject: {
            status: 'any',
            search_by: 'title',
            query: '',
            per_page: '10'
        }
    }),
    watch: {
        searchReturnObject: {
            immediate: false,
            handler(value) {
                this.$emit('updateSearch', value)
            },
            deep: true
        }
    },
    async mounted() {
        await this.initQueryParams()
        this.initMobileState()
        this.query = this.searchReturnObject.query;
        window.addEventListener('resize', this.initMobileState)
    },

    methods: {
        initQueryParams() {
            return new Promise((resolve) => {
                if (this.$route.query.length > 0) {
                    this.searchReturnObject.status = this.$route.query.status.length > 0 ? this.$route.query.status : 'any'
                    this.searchReturnObject.search_by = this.$route.query.search_by.length > 0 ? this.$route.query.search_by : 'title'
                    this.searchReturnObject.query = this.$route.query.query.length > 0 ? this.$route.query.query : ''
                    this.$forceUpdate()
                    resolve()
                }
            })
        },
        debounceSearchInput: debounce((e) => {
            this.searchReturnObject.query = e;
        }, 1000),
        searchFocus(val) {
            this.focusState = val === 'open';
        },
        initMobileState() {
            this.mobileState = window.innerWidth < 996;
        }
    }
}
</script>
