<template>
    <main-layout
        :settings-breadcrumbs="settingsBreadcrumbs"
        :total="orders.total"
    >
        <template #content>
            <section class="my-orders">
                <loading-overlay v-if="loading" />

                <order-filter @updateSearch="onUpdateSearch" />

                <div
                    v-if="!orders.total && !loading"
                    class="no-results"
                >
                    <img
                        src="@/assets/img/account/acc_nodata.svg"
                        alt="No Data"
                        class="no-results__img"
                    >
                    <h1 class="no-results__title no-results__title_question">
                        {{ getEmptyTitleDescription().title }}
                    </h1>
                    <p class="no-results__description">
                        {{ getEmptyTitleDescription().description }}
                    </p>
                </div>

                <div class="orders__list">
                    <div
                        v-for="(order, index) in orders.data"
                        :key="index"
                        class="wrapper"
                    >
                        <orders-card
                            :order="order"
                            :index="index"
                            :offset="offset"
                            :page="page"
                            mode="list"
                        />
                    </div>
                </div>
            </section>
            <cashback />
        </template>
    </main-layout>
</template>

<script>
import Api from '@/helpers/api/index.js'

import MainLayout from '@/layouts/MainLayout';
import Cashback from '@/components/onpage/Cashback'
import OrdersCard from '@/components/account/MyOrdersCard'
import OrderFilter from '@/components/account/OrderFilter'
import LoadingOverlay from '@/components/LoadingOverlay';

import { eventBus } from '@/helpers/event-bus'
import filtersMixin from '@/mixins/filtersMixin.js'

export default {
    components: {
        Cashback,
        MainLayout,
        OrdersCard,
        LoadingOverlay,
        OrderFilter
    },
    mixins: [filtersMixin],
    data() {
        return {
            orders: {},
            page: 1,
            loading: true,
            searchObject: {}
        }
    },
    computed: {
        settingsBreadcrumbs() {
            return {
                title: 'My Orders',
                steps: [
                    {
                        title: 'Home',
                        url: '/'
                    },
                    {
                        title: 'Account',
                        url: '/cp/account'
                    },
                    {
                        title: 'My Orders',
                        url: '/cp/orders'
                    }
                ],
                excerpt: 'You can unfold the details by clicking the arrow, then go to the order details screen by clicking "more info". There, you have a range of tools to control your order.',
                additionalClass: 'bc-horizontal bc-tickets'
            }
        },
        offset() {
            return this.orders.per_page * (this.page - 1)
        }
    },
    mounted() {
        eventBus.$on('DeletedOrder', () => {
            this.getData()
        });
        this.getData()
    },
    methods: {
        getEmptyTitleDescription() {
            const obj = {}
            if (this.searchObject.status === 'any' || !this.searchObject.status) {
                obj.title = 'You need to place your first order'
            }
            switch (this.searchObject.status) {
            case 'PENDING':
                obj.title = 'No data'
                obj.description = 'You have no orders that are currently in progress'
                break;
            case 'COMPLETE':
                obj.title = 'No data'
                obj.description = 'You have no orders that are currently in progress'
                break;
            case 'any':
                obj.title = 'No orders'
                obj.description = 'Start off by placing an order'
                break;
            default:
                obj.title = 'No data'
                obj.description = `You have no orders in ${this.searchObject.status} status`
            }
            return obj
        },
        getData() {
            const vm = this;
            Api.post('/api/cp/order/list-orders', this.searchObject)
                .then((response) => {
                    vm.orders = response.data;
                })
                .catch((error) => {
                    console.log(error)
                })
                .finally(() => {
                    vm.loading = false
                })
        },
        initQueryParams() {
            return new Promise((resolve) => {
                const url = this.$route.query
                if (url.page) {
                    this.searchObject.page = url.page ? url.page : 1
                    this.page = Number(url.page)
                }

                this.searchObject.status = url.status ? url.status : 'any'
                this.searchObject.search_by = url.search_by ? url.search_by : 'title'
                this.searchObject.query = url.query ? url.query : ''
                this.searchObject.per_page = url.per_page ? url.per_page : 10
                resolve()
            })
        },
        async onUpdateSearch(value) {
            this.page = 1
            this.$router.push({
                name: 'orders',
                query: { ...value }
            })
            this.initQueryParams()
            this.getData()
        }
    }
}
</script>
